<template>
  <div id="app">
    <p>
      Welcome to your Vue.js app with dropzone!
    </p>
    <dropzone ref="myUniqueID" id="myVueDropzone" url="https://httpbin.org/post" v-on:vdropzone-success-multiple="success"></dropzone>
    <button v-on:click="setValue">setValue</button>
  </div>
</template>

<script>
  import Dropzone from '../src/index'
  export default {
    name: 'MainApp',
    components: {
      Dropzone
    },
    data () {
      return {
        processQueue: false,
        fa: true
      }
    },
    methods: {
      'setValue': function () {
        this.$refs.myUniqueID.setOption('headers', {auth: 123})
      },
      'deleteImage': function () {
        console.log('yep')
      },

      'success': function (file, xhr, formData) {
        console.log('succees')
      },
      'process': function () {
        this.$refs.myUniqueID.processQueue()
      }
    }
  }
</script>

<style scoped>
  @import url('../node_modules/dropzone/dist/dropzone.css');
  @import 'https://fonts.googleapis.com/css?family=Roboto';
</style>


